<template>
  <div class="ViewProject">
    <el-card class="box-card">
      <span>请输入项目名称 &emsp;</span>
      <el-input placeholder="请输入关键字" v-model="input" clearable style="width:200px" size="mini"></el-input>&emsp;
      <el-button type="text" class="btn btn-primary">搜索</el-button>
      <el-button
        style="float:right"
        class="btn btn-primary"
        type="text"
        @click.stop="taskMaintain"
      >新建责任田</el-button>
      <el-dialog
        title="新建责任田"
        :visible.sync="dialogNewField"
        :modal-append-to-body="false"
        :append-to-body="true"
      >
        <el-form :model="newField" ref="newField" :rules="rules">
          <el-form-item label="责任田名称" label-width="120px" prop="name">
            <el-input v-model="newField.name" clearable></el-input>
          </el-form-item>
          <el-form-item label="是否启用" label-width="120px" prop="status">
            <el-switch v-model="newField.status"></el-switch>
          </el-form-item>
          <el-form-item label="田主" label-width="120px" prop="principal">
            <el-select v-model="newField.principal" placeholder="选择田主">
              <el-option label="张三" value="zhangsan"></el-option>
              <el-option label="李四" value="lisi"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="添加成员" label-width="120px">
            <el-input style="width:200px" v-model="memberInput"></el-input>&emsp;
            <el-button type="primary" @click.stop="addMember">添加</el-button>
          </el-form-item>
          <el-form-item label="成员" label-width="120px">
            <el-tag
              v-for="tag in newField.member"
              :key="tag.name"
              closable
              @close="handleClose(tag)"
              type="success"
              :disable-transitions="false"
              style="margin-right:4px"
            >{{tag}}</el-tag>
          </el-form-item>
          <el-form-item label="责任田描述" label-width="120px" prop="message">
            <el-input type="textarea" autosize placeholder="请输入内容" v-model="newField.message"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="submitForm('newField')">立即创建</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </el-card>
    <el-card class="box-card">
      <el-table :data="tableData" stripe style="width: 100%" :cell-style="{padding:0}">
        <el-table-column prop="id" label="责任田编码" width="180"></el-table-column>
        <el-table-column prop="name" label="责任田名称" width="180"></el-table-column>
        <el-table-column prop="project" label="所属项目"></el-table-column>
        <el-table-column prop="message" label="备注信息"></el-table-column>
        <el-table-column prop="principal" label="田主"></el-table-column>
        <el-table-column prop="status" label="是否启用"></el-table-column>
        <el-table-column fixed="right" label="操作" width="120">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="修改" placement="top-start">
              <el-button
                @click.stop="editField(scope.row)"
                type="success"
                size="mini"
                class="el-icon-edit"
              ></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="删除" placement="top-start">
              <el-button
                @click.stop="deleteField(scope.row)"
                type="danger"
                size="mini"
                class="el-icon-delete"
              ></el-button>
            </el-tooltip>
            <el-dialog
              title="责任田修改"
              :visible.sync="dialogVisible"
              width="50%"
              :modal-append-to-body="false"
              :append-to-body="true"
            >
              <el-form :model="editFieldInfo">
                <el-form-item label="责任田名称" label-width="120px">
                  <el-input v-model="editFieldInfo.name" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="是否启用" label-width="120px">
                  <el-switch v-model="editFieldInfo.status"></el-switch>
                </el-form-item>
                <el-form-item label="田主" label-width="120px">
                  <el-select v-model="editFieldInfo.principal" placeholder="选择田主">
                    <el-option label="张三" value="zhangsan"></el-option>
                    <el-option label="李四" value="lisi"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="添加成员" label-width="120px">
                  <el-input style="width:200px" v-model="memberInput"></el-input>&emsp;
                  <el-button type="primary" @click.stop="addMember">添加</el-button>
                </el-form-item>
                <el-form-item label="成员" label-width="120px">
                  <el-tag
                    v-for="tag in newField.member"
                    :key="tag.name"
                    closable
                    @close="handleClose(tag)"
                    type="success"
                    :disable-transitions="false"
                    style="margin-right:4px"
                  >{{tag}}</el-tag>
                </el-form-item>
                <el-form-item label="责任田描述" label-width="120px">
                  <el-input
                    type="textarea"
                    autosize
                    placeholder="请输入内容"
                    v-model="editFieldInfo.message"
                  ></el-input>
                </el-form-item>
              </el-form>
              <span slot="footer" class="dialog-footer">
                <el-button @click.stop="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click.stop="dialogVisible = false">保 存</el-button>
              </span>
            </el-dialog>
          </template>
        </el-table-column>
      </el-table>
      <CutPage v-on:currentPage="currentPage" style="margin-top:20px"></CutPage>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      memberInput: "",
      newField: {
        name: "",
        message: "",
        status: "",
        principal: "",
        member: ["张三", "李四"]
      },
      dialogNewField: false,
      dialogVisible: false,
      editFieldInfo: {},
      input: "",
      tableData: [
        {
          id: "1",
          name: "第一责任田",
          project: "我属于第一个项目",
          message: "第一个项目的备注信息",
          status: "是",
          principal: "lwx789456"
        },
        {
          id: "2",
          name: "第二责任田",
          project: "我属于第二个项目",
          message: "第二个项目的备注信息",
          status: "是",
          principal: "lwx789456"
        },
        {
          id: "3",
          name: "第三责任田",
          project: "我属于第三个项目",
          message: "第三个项目的备注信息",
          status: "否",
          principal: "lwx789456"
        },
        {
          id: "4",
          name: "第四责任田",
          project: "我属于第四个项目",
          message: "第四个项目的备注信息",
          status: "是",
          principal: "lwx789456"
        },
        {
          id: "5",
          name: "第五责任田",
          project: "我属于第五个项目",
          message: "第五个项目的备注信息",
          status: "否",
          principal: "lwx789456"
        },
        {
          id: "6",
          name: "第六责任田",
          project: "我属于第六个项目",
          message: "第六个项目的备注信息",
          status: "是",
          principal: "lwx789456"
        },
        {
          id: "7",
          name: "第七责任田",
          project: "我属于第七个项目",
          message: "第七个项目的备注信息",
          status: "是",
          principal: "lwx789456"
        }
      ],
      rules: {
        name: [
          { required: true, message: "请输入责任田名称", trigger: "blur" }
        ],
        status: [{ required: true, message: "请选择状态", trigger: "blur" }],
        principal: [{ required: true, message: "请选择田主", trigger: "blur" }],
        message: [
          { required: true, message: "请输入责任田描述", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    currentPage(value) {
      console.log(value);
    },
    addMember() {
      this.newField.member.push(this.memberInput);
      this.memberInput = "";
    },
    handleClose(tag) {
      this.newField.member.splice(this.newField.member.indexOf(tag), 1);
    },
    deleteField(row) {
      this.$confirm("确认删除？")
        .then(_ => {
          for (let i = 0; i < this.tableData.length; i++) {
            if (this.tableData[i].id == row.id) {
              this.tableData.splice(i, 1);
            }
          }
        })
        .catch(_ => {});
    },
    editField(row) {
      console.log(row);
      this.dialogVisible = true;
      this.editFieldInfo = row;
    },
    taskMaintain() {
      this.dialogNewField = true;
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
          this.dialogNewField = false;
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
</style>